import CloudStorageForm from '../components/CloudStorageForm'
import '../styles/cloud-storage.scss'
import classNames from 'classnames'
import PropTypes from 'prop-types'
import React from 'react'

const CLOUD_LIST = [
  {
    icon: require('../statics/default.svg'),
    name: 'Default',
    component: (
      <div className="cloud-storage-square">
        The picture will be uploaded to the sm.ms cloud storage by default.
      </div>
    )
  },
  {
    icon: require('../statics/qiniu.png'),
    name: 'Qiniu Cloud',
    component: (
      <CloudStorageForm
        name="qiniu"
        fields={[
          { name: 'accessKey', type: 'password' },
          { name: 'secretKey', type: 'password' },
          'bucket',
          'domain'
        ]}
      />
    )
  },
  {
    icon: require('../statics/upyun.jpg'),
    name: 'Upyun Cloud',
    component: (
      <CloudStorageForm
        name="upyun"
        fields={[
          'domain',
          'operator',
          { name: 'password', type: 'password' },
          'bucket'
        ]}
      />
    )
  },
  {
    icon: require('../statics/aliyun.jpg'),
    name: 'Aliyun OSS',
    component: (
      <CloudStorageForm
        name="aliyun"
        fields={[
          { name: 'accessKeyId', type: 'password' },
          { name: 'accessKeySecret', type: 'password' },
          'bucket',
          'region'
        ]}
      />
    )
  },
  {
    icon: require('../statics/tencent.jpg'),
    name: 'Tecent COS',
    component: (
      <CloudStorageForm
        name="tecent"
        fields={[
          { name: 'SecretId', type: 'password' },
          { name: 'SecretKey', type: 'password' },
          'Bucket',
          'Region'
        ]}
      />
    )
  },
  {
    icon: require('../statics/aws.png'),
    name: 'Amazon S3',
    component: null,
    disable: true
  },
  {
    icon: require('../statics/imgur.jpg'),
    name: 'Imgur',
    component: null,
    disable: true
  },
  {
    icon: require('../statics/flickr.jpg'),
    name: 'Flickr',
    component: null,
    disable: true
  }
]

const CloudList = function ({ component, disable, icon, name }, toggleCloudStorageForm) {
  return (
    <div
      onClick={() => { toggleCloudStorageForm(component) }}
      className={classNames({ disable })}
      key={name}>
      <img src={icon} alt={name} />
      <span>{name}</span>
    </div>
  )
}

CloudList.propTypes = {
  component: PropTypes.object,
  disable: PropTypes.bool,
  icon: PropTypes.string,
  name: PropTypes.string
}

export default class CloudStorage extends React.Component {
  state = {
    component: CLOUD_LIST[1].component
  }

  toggleCloudStorageForm = component => {
    this.setState({ component })
  }

  render () {
    const { toggleCloudStorageForm } = this
    return (
      <section
        className={classNames('cloud-storage', { 'no-auth': false })}>
        <aside>
          {CLOUD_LIST.map(cloud => CloudList(cloud, toggleCloudStorageForm))}
        </aside>
        <main>
          {this.state.component}
        </main>
      </section>
    )
  }
}



