import CloudStorage from '../components/CloudStorage'
import { firebase } from '../utils/auth'
import { Button, message } from 'antd'
import { observer } from 'mobx-react'
import notice from '../utils/notice'
import user from '../stores/user'
import '../styles/setting.scss'
import { t } from 'i18next'
import React from 'react'

class GrantAuthorizationButton extends React.Component {
  auth = () => {
    const provider = new firebase.auth.GoogleAuthProvider()
    firebase.auth().signInWithPopup(provider).then(result => {
      user.requestUserInfo()
    }, err => {
      if (err) {
        notice({ title: t('auth.error') }, function () {
          message.error(t('auth.error'))
        })
      }
    })
  }

  render () {
    const { auth } = this
    return (
      <div className="auth">
        <div align="center">
          <p>Avi requires Google authorization to use custom cloud storage services.</p>
          <Button onClick={auth} type="primary" ghost>Google Authorize</Button>
        </div>
      </div>
    )
  }
}

@observer
export default class Setting extends React.Component {
  render () {
    return (
      <section className="setting">
        <div className="container">
          <CloudStorage />
          {!user.token && <GrantAuthorizationButton />}
        </div>
      </section>
    )
  }
}



