import '../styles/cloud-storage-form.scss'
import { Form, Input, Button } from 'antd'
import PropTypes from 'prop-types'
import { t } from 'i18next'
import React from 'react'

const FormItem = Form.Item

const formItemLayout = {
  labelCol: {
    sm: { span: 6 }
  },
  wrapperCol: {
    sm: { span: 18 }
  }
}

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0
    },
    sm: {
      span: 16,
      offset: 20
    }
  }
}

const StorageFormItem = function (field, getFieldDecorator) {
  const name = field.name || field
  const type = field.type || 'text'
  return (
    <FormItem {...formItemLayout} label={name} key={name}>
      {getFieldDecorator(name, {
        rules: [{ required: true, whitespace: true, message: t('setting.cloud.validate', { field: name }) }]
      })(<Input type={type} />)}
    </FormItem>
  )
}

class CloudStorageForm extends React.Component {
  static propTypes = {
    fields: PropTypes.array,
    form: PropTypes.object
  }

  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log(values)
      }
    })
  }

  render () {
    const { props: { fields, form: { getFieldDecorator } } } = this

    return (
      <Form onSubmit={this.handleSubmit} className="cloud-storage-form">
        {fields.map(field => StorageFormItem(field, getFieldDecorator))}
        <FormItem {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit">Register</Button>
        </FormItem>
      </Form>
    )
  }
}

export default Form.create()(CloudStorageForm)



